<template>
  <div class="mainTable">
    <div class="mainTable_title">
      <div>{{tempName}}</div>
    </div>
    <div class="mainTable_inputBox">
      <el-form
        :model="formData"
        :rules="rules"
        size="medium"
        ref="formData"
        label-width="120px"
        label-position="right"
      >
        <el-form-item label="" prop="name">
          <el-input v-model="formData.name" placeholder="" clearable></el-input>
        </el-form-item>
        <el-form-item label="POS" prop="name">
          <el-input v-model="formData.name" placeholder="POS" clearable></el-input>
        </el-form-item>
        <!-- <div class="posTxt fontRed">POS.</div> -->
        <el-form-item label="" prop="name">
          <el-input v-model="formData.name" placeholder="" clearable></el-input>
        </el-form-item>
        <div class="posTxt" style="color:#606266;">.</div>
      </el-form>
      <div slot="footer" class="dialog-footer ac">
        <el-button type="primary" @click="submitForm('formData')"> </el-button>
        <el-button class="ml20" @click="cancelForm"> </el-button>
      </div>
    </div>
    <el-tabs tab-position="left" @tab-click="tabClick" value="store" stretch>
      <el-tab-pane name="store" label="">
        <div class="mainTable_search">
          <search-base :searchList="searchList" @searchEvent="searchEvent"></search-base>
        </div>
        <div class="mainTable_content">
          <table-base
            :tableBut="tableBut"
            :tableSelection="tableSelection"
            :tableExpand="tableExpand"
            :tableIndex="tableIndex"
            :tableHead="tableHead"
            :tableData="tableData"
            :tableOperate="tableOperate"
            :tableSize="tableSize"
            :tableTotal="tableTotal"
            @butEvent="butEvent"
            @operateEvent="operateEvent"
            @selectionEvent="selectionEvent"
            @filterEvent="filterEvent"
            @sizeChangeEvent="sizeChangeEvent"
            @currentChangeEvent="currentChangeEvent"
          ></table-base>
        </div>
      </el-tab-pane>
      <el-tab-pane name="clerk" label="">
        <div class="mainTable_search">
          <search-base :searchList="searchList" @searchEvent="searchEvent"></search-base>
        </div>
        <div class="mainTable_content">
          <table-base
            :tableBut="tableBut"
            :tableSelection="tableSelection"
            :tableExpand="tableExpand"
            :tableIndex="tableIndex"
            :tableHead="tableHead"
            :tableData="tableData"
            :tableOperate="tableOperate"
            :tableSize="tableSize"
            :tableTotal="tableTotal"
            @butEvent="butEvent"
            @operateEvent="operateEvent"
            @selectionEvent="selectionEvent"
            @filterEvent="filterEvent"
            @sizeChangeEvent="sizeChangeEvent"
            @currentChangeEvent="currentChangeEvent"
          ></table-base>
        </div>
      </el-tab-pane>
    </el-tabs>
  </div>
</template>
<script>
export default {
  name: "writeList",
  data() {
    return {
      tempName: "",
      // 
      rules: {
        name: [
          { required: true, message: "", trigger: "blur" },
          { min: 3, max: 5, message: " 3  5 ", trigger: "blur" }
        ]
      },
      type: "",
      searchData: {},

      formData: {},
      dialogFormVisible: false,
      tableSelection: false,
      tableExpand: false,
      tableIndex: true,
      tableSize: 10,
      tableTotal: 30,

      searchList: [
        {
          id: "input",
          type: "input",
          name: "input",
          title: "",
          placeholder: ""
        },
        {
          id: "input",
          type: "input",
          name: "input",
          title: "",
          placeholder: ""
        },
        {
          id: "daterange",
          type: "timeDate",
          name: "daterange",
          title: "",
          bplaceholder: "",
          eplaceholder: "",
          timeType: "daterange"
        }
      ],

      tableBut: [{ name: " ", value: "export", show: true }],
      tableHead: [
        { name: "", value: "name" },
        { name: "", value: "date" },
        { name: "POS", value: "address" },
        { name: "", value: "progress" },
        { name: "", value: "id" },
        { name: "", value: "id" },
        { name: "", value: "id" },
        { name: "", value: "id" },
        { name: "", value: "id" },
        {
          name: "",
          value: "status",
          format: (row, column, cellValue, index) => {
            return (cellValue = cellValue == 1 ? "" : "");
          },
          filter: [{ text: "", value: "1" }, { text: "", value: "2" }]
        },
        { name: "", value: "id" }
      ],
      tableData: []
    };
  },
  // 
  components: {
  },
  methods: {
    tabClick(e) {
      this.type = e.name;
      console.log(e.name);
      if (e.name == "clerk") {
        this.tableHead = [
          { name: "", value: "" },
          { name: "", value: "" },
          { name: "", value: "" },
          { name: "", value: "" },
          { name: "", value: "" },
          { name: "", value: "" },
          { name: "", value: "" }
        ];
      } else {
        this.tableHead = [
          { name: "", value: "" },
          { name: "", value: "" },
          { name: "POS", value: "" },
          { name: "", value: "" },
          { name: "", value: "" },
          { name: "", value: "" },
          { name: "", value: "" },
          { name: "", value: "" },
          { name: "", value: "" },
          {
            name: "",
            value: "status",
            format: (row, column, cellValue, index) => {
              return (cellValue = cellValue == 1 ? "" : "");
            },
            filter: [{ text: "", value: "1" }, { text: "", value: "2" }]
          },
          { name: "", value: "id" }
        ];
      }
    },

    searchEvent(e) {
      console.log(e);
    },
    butEvent(e) {
      this[e.eventName](e.val);
    },
    filterEvent(e) {
      console.log(e);
    },
    selectionEvent(e) {
      console.log(e);
    },
    operateEvent(e) {
      this[e.eventName](e.val);
    },
    export(e) {
      this.$exportData(this.tableHead, this.tableData, this.tempName);
    },
    check(e) {
      console.log(e);

      this.dialogFormVisible = true;
    },

    sizeChangeEvent(e) {
      console.log(` ${e} `);
    },
    currentChangeEvent(e) {
      console.log(`: ${e}`);
    },

    cancelForm() {
      this.dialogFormVisible = false;
    },
    submitForm() {
      this.dialogFormVisible = false;
    }
  }
};
</script>
<style lang="scss" scoped>
.mainTable_inputBox {
  width: 560px;
  padding-bottom: 40px;
}
.el-tabs {
  padding-top: 30px;
  border-top: 1px solid rgba(192, 204, 218, 0.5);
}
.posTxt {
  padding: 0 0 30px 120px;
}
</style>



